* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.react h2 {
  color: blue;
  margin-top: 30px;
}

.react img {
  width: 300px;
}
.react a {
  text-decoration: none;
  display: block;
  width: 120px;
  height: 50px;
  background-color: black;
  color: white;
  line-height: 50px;
  margin: 20px auto;
}

.page {
  border-top: 1px solid #333;
  padding: 20px 0;
}